"use client";

import { Box, Typography, Paper } from "@mui/material";

const FlightCard = ({ fromCity, toCity, airline, price }) => {
  return (
    <Paper
      elevation={1}
      sx={{
        display: "flex",
        justifyContent: "space-around",
       
        p: 2,
        borderRadius: "12px",
        minHeight: "70px",
        border: "1px solid #e5e7eb",
        "&:hover": { boxShadow: 3, transition: "0.2s" },
      }}
    >
      {/* From */}
      <Box sx={{ display: "flex", flexDirection: "column", justifyContent: "center" }}>
        <Typography fontSize="14px" fontWeight="600">
          {fromCity}
        </Typography>
        {airline && (
          <Typography
            fontSize="11px"
            color="text.secondary"
            sx={{
              maxWidth: "120px",
              overflow: "hidden",
              textOverflow: "ellipsis",
              whiteSpace: "nowrap",
            }}
          >
            {airline}
          </Typography>
        )}
      </Box>

      {/* Middle: Icon */}
      <Box sx={{ display: "flex", alignItems: "center", justifyContent: "center", px: 5 }}>
        <svg xmlns="http://www.w3.org/2000/svg" color="blue" width="15" height="15" viewBox="0 0 24 24">
          <path
            fill="currentColor"
            d="M20.56 3.91c.59.59.59 1.54 0 2.12l-3.89 3.89l2.12 9.19l-1.41 1.42l-3.88-7.43L9.6 17l.36 2.47l-1.07 1.06l-1.76-3.18l-3.19-1.77L5 14.5l2.5.37L11.37 11L3.94 7.09l1.42-1.41l9.19 2.12l3.89-3.89c.56-.58 1.56-.58 2.12 0"
          />
        </svg>
      </Box>

      {/* To + Price */}
      <Box sx={{ display: "flex", flexDirection: "column",px: 1, justifyContent: "center", textAlign: "right" }}>
        <Typography fontSize="14px" fontWeight="600">
          {toCity}
        </Typography>
        <Typography fontSize="11px" color="text.secondary">
          From USD {price}
        </Typography>
      </Box>

    </Paper>
  );
};

export default FlightCard;
