"use client";

import React from "react";
import { Box, Grid, Typography, Container } from "@mui/material";
import FlightCard from "./FlightCard";

const flightData = [
  { fromCity: "Lahore", toCity: "Dubai", airline: "Pakistan International Airlines", price: 100 },
  { fromCity: "Kuala Lumpur", toCity: "Dubai", airline: "Malaysia Airlines", price: 620 },
  { fromCity: "Berlin", toCity: "Istanbul", airline: "Turkish Airlines", price: 600 },
  { fromCity: "Dubai", toCity: "Sharjah", airline: "Emirates", price: 460 },
  { fromCity: "Dhaka", toCity: "Jeddah", airline: "Australian Airlines", price: 385 },
  { fromCity: "Delhi", toCity: "Moscow", airline: "Air India Limited", price: 760 },
  { fromCity: "Manila", toCity: "Dubai", airline: "Air Philippines", price: 450 },
  { fromCity: "Surabaya", toCity: "New York", airline: "American Airlines", price: 800 },
  { fromCity: "Berlin", toCity: "Dubai", airline: "Air Arabia", price: 240 },
];

const FeaturedFlights = () => {
  return (
    <Box sx={{ py: 4, px: { xs: 2, md: 4, lg: 10 } }}>
      <Container maxWidth="lg">
        <Box sx={{ mb: 3 }}>
          <Typography variant="h6" sx={{ fontWeight: "bold" }}>
            Featured Flights
          </Typography>
          <Typography variant="body2" color="text.secondary">
            These alluring destinations are picked just for you.
          </Typography>
        </Box>

        <Grid container fullwidth spacing={1} >
          {flightData.map((flight, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <FlightCard
                fromCity={flight.fromCity}
                toCity={flight.toCity}
                airline={flight.airline}
                price={flight.price}
              
              />
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
};

export default FeaturedFlights;
